<template>
	<v-dialog
    v-model="dialog"
    width="500"
    persistent>
    <template v-slot:activator="{ on }">
			<v-btn
				v-on="on"
        icon
        class="white mr-3"
        color="primary">
        <v-icon small>mdi-pencil</v-icon>
      </v-btn>
    </template>

    <v-card>
      <div class="pa-2 text-center">
        <span class="title">Edit Sprint</span>
      </div>
      <v-form v-model="valid" ref="form">
        <v-card-text>
          <v-text-field
            v-model="data.nama"
            label="Nama"
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field> 
          <v-dialog
              ref="dialog"
              v-model="modal"
              :return-value.sync="data.deadline"
              persistent
              width="290px">
              <template v-slot:activator="{ on }">
                <v-text-field
                  v-model="data.deadline"
                  label="Deadline"
                  prepend-icon="mdi-calendar"
                  readonly
                  v-on="on"
                  :rules="requiredRule"
                ></v-text-field>
              </template>
              <v-date-picker v-model="data.deadline" scrollable>
                <v-spacer></v-spacer>
                <v-btn text color="primary" @click="modal = false">Cancel</v-btn>
                <v-btn text color="primary" @click="$refs.dialog.save(data.deadline)">OK</v-btn>
              </v-date-picker>
            </v-dialog>  

          <Notif :msg="msg" :status="status" @visible="visible" />
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="dialog = false">
            kembali
          </v-btn>
          <v-btn
            :disabled="!valid"
            :loading="isLoading"
            color="primary"
            @click.prevent="validate"
            type="submit"
            depressed>
            simpan
          </v-btn>
        </v-card-actions>
      </v-form>
    </v-card>
  </v-dialog>
</template>

<script>
  import { mapState } from 'vuex'
  import Notif from '@/components/Notif'

  export default {
    props: ['data'],

    data () {
      return {
        dialog: false,
        valid: true,
        modal: false,
        requiredRule: [v => !!v || 'Data harus diisi'],
        msg: {
          success: 'Berhasil diubah',
          error: 'Gagal diubah',
          visible: false
        }
      }
    },

    components: {
      Notif
    },

    computed: {
      ...mapState('dataStartupSprint', ['isLoading', 'status']),      
    },

    watch: {
      status(val) {
        if (val === null) this.dialog = false
      }
    }, 

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          const data = {
            id: this.data.id,
            nama: this.data.nama,
            deadline: this.data.deadline
          }
          this.$store.dispatch('dataStartupSprint/putDataSprint', data)
          this.$store.dispatch('dataStartupSprint/updateIsLoading', true)
          this.msg.visible = true
        }
      },
      visible(val) {
        this.msg.visible = val
        this.$store.commit('dataStartupSprint/updateStatus', null)
      }
    }
  }
</script>