<template>
	<div>
		<TambahSprint />
		<v-card>
			<v-card-title class="py-2">Sprint</v-card-title>
			<v-card-text>
				<Loader v-if="isLoading" />
				<v-data-table
					v-else
					class="mt-0"
					:headers="header"
					:items="dataSprint"
					:items-per-page="10">	
					<template v-slot:item.no="{item}">
						{{dataSprint.map(x => x.id).indexOf(item.id)+1}}
					</template>
					<template v-slot:item.progress="{item}">
						<v-btn
							depressed
							color="cyan"
							@click="$router.push({ name: 'data-progress-startup', params: { sprint_id: item.id } })"
							dark small>
							manage
						</v-btn>
					</template>
					<template v-slot:no-data>
						<v-alert type="info" class="mt-4">
							<div>Data tidak ditemukan</div>
						</v-alert>
					</template>
					<template v-slot:item.action="{item}">
						<EditSprint :data="{item}" />
						<HapusSprint :id="item.id" />
					</template>
				</v-data-table>
			</v-card-text>
		</v-card>
	</div>
</template>

<script>
	import { mapState } from 'vuex'
	import Loader from '@/components/Loader'
  import TambahSprint from './TambahSprint'
  import EditSprint from './EditSprint'
  import HapusSprint from './HapusSprint'

	export default{
		data: () => ({
			header: [
				{ text: 'No', value: 'no', sortable: false },
				{ text: 'Sprint', value: 'nama', sortable: false },
				{ text: 'Deadline', value: 'deadline', sortable: false },
				{ text: 'Actions', value: 'action', sortable: false },
				{ text: 'Progress', value: 'progress', sortable: false },
			]
		}),

		components: {
			Loader,
			TambahSprint,
			EditSprint,
			HapusSprint
		},	

		created() {
			this.$store.dispatch('dataStartupSprint/getDataSprint')
			this.$store.dispatch('dataStartupSprint/updateIsLoading', true)
		},	

		computed: {
			...mapState('dataStartupSprint', ['dataSprint', 'isLoading'])
		}
	}
</script>